import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'booking-flow',
  templateUrl: 'bookingflow.component.html'
})

export class BookingFlowComponent implements OnInit {
  @Output() bookingFlow: EventEmitter<any> = new EventEmitter<any>()
  constructor() {
  }

  ngOnInit() {
  }

  existingCustomerHandle() {
    this.bookingFlow.emit({'clickType': 1});
  }

  newCustomerHandle() {
    this.bookingFlow.emit({'clickType': 2});
  }
}
